//НАЧАЛО - СЕКЦИЯ footer *****************************************************

footer {
    height: 48px;
    background: #D8DEC5;
  }
  .footer__container {
    width: 100%;
    height: 48px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    p {
      font-weight: normal;
      font-size: 14px;
      line-height: 120%;
      text-align: center;
      color: #555555;
    }
  }
  #modal-confidentiality {
    display: none;
    max-width: 900px;
    width: 90%;
    padding: 40px;
    h2 {
      margin-bottom: 30px;
      line-height: 40px;
      font-size: 30px;
      text-align: center;
    }
    p {
      margin-bottom: 15px;
      line-height: 30px;
      font-size: 20px;
    }
  }
  
//НАЧАЛО - СЕКЦИЯ media footer*****************************************************

@media (max-width: 1200px){
    footer {
      height: auto;
    }
    .footer__container {
      height: auto;
      padding: 20px;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      P{
        margin: 10px 15px;
      }
  }
  
  }
  
  @media (max-width: 1026px){
    .footer__container {
      position: relative;
      z-index: 5;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      P{
        margin: 10px 15px;
      }
  }
  
  }
  
  @media (max-width: 975px){
    .footer__container {
      P{
